export const formatTime = (data) => {
  let time = new Date().getTime() - data;
  data = Number(data);
  if (time < 3000) {
    return "刚刚";
  }
  if (time < 60000) {
    return new Date(data).getSeconds() + "秒前";
  }
  if (time < 600000) {
    return new Date(time) + "分钟前"
  }
  if (time < 86400000) {
    return new Date(time) + "小时前"
  }
  let y = new Date(data).getFullYear();
  let m = new Date(data).getMonth() + 1;
  let d = new Date(data).getDate();
  return y + "/" + m + "/" + d;
};



