<template>
  <div id="post">
    <reList v-if="show">标题</reList>
    <p class="m-t-10">内容：</p>
    <div class="post-input" contenteditable="true">
      <p>
        <br>
      </p>
    </div>
    <input type="button" class="btn" value="发 帖">
  </div>
</template>
<script>
  import reList from "@/components/reList"
  export default({
    name: 'index',
    data(){
      return {
        show: false,
        pid: "",
        fid: "",
        cid: ""
      }
    },
    created(){
      document.title = "发帖";
      this.pid = this.$route.query.pid;
      this.fid = this.$route.query.fid;
      this.cid = this.$route.query.cid;
      if (!this.pid) {
        this.show = true;
      }

    },
    components: {reList}
  })
</script>
<style scoped>
  #post {
    margin: 20px auto;
    width: 90%;
  }

  .post-input {
    height: 50px;
    padding: 5px;
    border: 1px solid darkcyan;
    border-radius: 5px;
  }

  .btn {
    margin-top: 20px;
  }
</style>



